.Header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.5rem 2rem;
  background-color: white;
  color: black;
  font-size: 1rem;
  font-weight: 500;
  position: sticky;
  top: 0;
  z-index: 100;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);

  &__profile {
    display: flex;
    align-items: center;
  }

  &__portrait {
    width: 50px;
    height: 50px;
    border-radius: 50%;
    object-fit: cover;
  }

  &__name {
    margin-left: 1rem;
    font-size: 1em;
  }
}
